*{
    box-sizing: border-box;
}
body{
    margin: 0;
    padding: 0;
    background-color: #eff3f7;
    height:100%;
}

html,
body,
#potato,
.container
{
height:100%;
display: flex;
justify-content: center;
}

.loader {
    width:100%;
    height:100%;
    display:flex;
    justify-content: center;
    align-items:center;
    font-weight:300;
}

.movies{
    display:flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap:wrap;
    padding:50px;
    padding-top: 70px;
    width:80%;
}
.movies .movie{
    width:45%;
    background-color: white;
    margin-bottom: 70px;
    display:flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding:20px;
    position:relative;
}

.movie img{
    position:relative;
    top:-50px;
    max-width: 150px;
    width:100%;
    max-width: 120px ;
    margin-right:20px;
}
.movie h3,
.movie h5{
    margin:0;
    font-weight: 300;
}

.movie h3{
    margin-bottom: 5px;
    font-size:24px;
}

.movie .movie__title,
.movie .movie__year {
    margin:0;
    font-weight: 300;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size:24px;
}

.movie .movie__year,
.movie .movie__genres{
    color:#b2b3bc;
}
.movie .movie__genres{
    list-style:none;
    padding:0;
    margin:5px 0;
    display: flex;

}

.movie__genres li {
    margin-right: 10px;
}